 세상을 떠났다. 죽은 뒤에도 한동안 인터넷에는 그의 이름이나 필명들만 언급, 소개되어도 익명의 악플과 조롱, 비방이 쏟아지기도 했다. 그의 이름은 2010년 이후에나 한국 사회에 공개될 수 있었다. 그가 떠난 뒤 동인련은 매년 4월 고 육우당 추모의 밤을 진행해 왔다. 이후 그는 고통받는 청소년 성(性) 소수자 인권의 상징이 됐다. ")]),_c('p',[_c('a',{attrs:{"target":"_blank","href":"https://librewiki.net/wiki/%EC%9C%A1%EC%9A%B0%EB%8B%B9"}},[_vm._v("리브레 위키 \"육우당\" 항목 링크")])]),_c('v-divider',{staticClass:"info my-10",staticStyle:{"opacity":"0.22"}}),_c('v-alert',{staticClass:"pt-10 px-10",attrs:{"border":"left","colored-border":"","color":"#CE93D8","elevation":"2"}},[_c('p',[_vm._v(" 육우당은 동성애자들이 멸시받지 않는 세상이 오게 해달라고 기도하며, 동성애자들을 정죄의 대상으로 여기는 기독교의 태도에 목숨을 잃었다. 하지만 18년이 지난 지금도 그의 울부짖는 영혼이 여전히 한국 사회를 떠돌고 있다. 하나님의 사랑이라는 이름으로 한 인간의 존엄한 삶을 짓밟는 기독교와 한국 사회는, 얼마나 더 많은 사람이 목숨을 내어놓아야 소수자들의 울부짖음을 들을 것인가? ")]),_c('div',{staticClass:"d-flex justify-end px-5 my-4"},[_vm._v(" – Hyerim Park ")])])],1)],1)],1)],1)],1)],1)}
